import React from 'react'
import { useAtom } from 'jotai'
import { currentObjectAtom, oasAtom } from 'store/labs/oas'
import { Divider, Header, Image, List, Segment } from 'semantic-ui-react'
import { LearningObject } from 'store/labs/types'
import { RouteComponentProps } from 'react-router-dom'
import { capitalize } from 'utils'
import SpacedContainer from 'styles/common'
import { ActivityType, SkillType } from 'components/Main/Activity/types'

type ObjectListParams = {
  list: LearningObject[]
}

type MatchParams = {
  level: ActivityType
  skill: SkillType
}

const learningBannerSrc = `/assets/img/learining-banner.jpg`

const ActivityList = ({ history, match }: RouteComponentProps<MatchParams>): JSX.Element => {
  const [oas] = useAtom(oasAtom)

  const { level, skill } = match.params

  const listOAs = oas[level][skill]
  const [, setCurrentOA] = useAtom(currentObjectAtom)

  const setCurrentObject = (object: LearningObject) => {
    setCurrentOA(object)
    history.push(`${skill}/object-detail`)
  }

  const ObjectList = ({ list }: ObjectListParams): JSX.Element => {
    return (
      <List>
        {list.map((object) => (
          <List.Item
            key={object.id}
            onClick={() => setCurrentObject(object)}
            style={{ cursor: 'pointer' }}
          >
            <List.Icon name="angle right" />
            <List.Content>
              <List.Header>{object.title}</List.Header>
            </List.Content>
            <Divider />
          </List.Item>
        ))}
      </List>
    )
  }

  return (
    <div>
      <Segment>
        <SpacedContainer>
          <Header>
            {level}: {capitalize(skill)}
          </Header>
          <Image src={learningBannerSrc} size="medium" alt="banner" verticalAlign="middle" />
        </SpacedContainer>

        <Divider fitted />

        <ObjectList list={listOAs} />
      </Segment>
    </div>
  )
}

export default ActivityList
