import React, { useState, useEffect } from 'react'
import { useAtom } from 'jotai'
import { Button, Divider, Form, FormGroup, Header } from 'semantic-ui-react'
import { RouteChildrenProps } from 'react-router-dom'
import { fetchLogin } from 'store/api'
import { currentUser, loadingAtom } from 'store'
import Container from 'styles/auth'

const Login = ({ history }: RouteChildrenProps): JSX.Element => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')

  const [loading] = useAtom(loadingAtom)
  const [user] = useAtom(currentUser)
  const [, login] = useAtom(fetchLogin)

  const nonEmpties = [username, password].filter(Boolean).length === 2

  useEffect(() => {
    if (user.id) {
      history.push('/')
    }
  }, [user, history])

  const onLogin = () => {
    console.log(`Login user ${username} with password ${password}`)
    login({ username, password })
  }
  console.log('<Login> rendered')
  return (
    <Container loading={loading}>
      <Header as="h1">Login</Header>
      <Divider />
      <Form>
        <FormGroup>
          <Form.Input
            label="Username"
            name="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </FormGroup>
        <FormGroup>
          <Form.Input
            label="Password"
            name="password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </FormGroup>
      </Form>
      <Button type="submit" disabled={!nonEmpties} onClick={onLogin}>
        Login
      </Button>
    </Container>
  )
}

export default Login
