import { atom } from 'jotai'
import { loadingAtom } from 'store'
import { currentUser } from 'store/auth'
import { User, UserBase } from 'store/auth/types'
import { LabActivity, LabActivityBase } from 'store/labs/types'

const API_URL = process.env.REACT_APP_API

export const fetchLogin = atom(null, async (_get, set, data: UserBase) => {
  set(loadingAtom, true)
  const body = JSON.stringify(data)

  const response = await fetch(`${API_URL}/auth/login`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body
  })

  const user: User = await response.json()
  set(currentUser, user)
  set(loadingAtom, false)
  console.log('User logged in', user)
})

export const fetchSaveLab = atom(null, async (_get, set, data: LabActivityBase) => {
  set(loadingAtom, true)
  const body = JSON.stringify(data)
  const response = await fetch(`${API_URL}/student/save-lab`, {
    method: 'POST',
    body
  })
  const lab: LabActivity = await response.json()
  console.log('Stored activity', lab)
  set(loadingAtom, false)
})
